body {
    margin: 0;
    font-family: Arial, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(135deg, #0078d7, #0050a0);
    color: white;
  }
  
  .login-container {
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    text-align: center;
    width: 300px;
    height: 300px;
    top: 35%;
    left: 20%;
  }
  
  .login-container h1 {
    position: absolute;
    top:35%;
    left: 39%;
    font-size: 24px;
  }
  
  .login-container img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-bottom: 15px;
  }
  
  .login-container input[type="password"] {
    position: absolute;
    width: 80%;
    padding: 10px;
    top: 55%;
    left: 7%;
    margin: 10px 0;
    border: none;
    border-radius: 10px;
    font-size: 16px;
  }
  
  .login-container button {
    position: absolute;
    padding: 15px 25px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    color: white;
    background-color: #0078d7;
    cursor: pointer;
    transition: background-color 0.3s;
    top: 75%;
    left: 36%;
  }
  
  .login-container button:hover {
    background-color: #0050a0;
  }
